

#progOutput tbody img{
  max-width: 200px;
  margin-bottom: 120px;
}

#progOutput table{
  overflow: auto;;
  border-style: solid;
  border-color:rgb(253, 254, 255);
  
}

#progOutput {

  grid-area: catdata;
  background-color: rgb(151, 187, 144);
 
}
#topContainer { 
  margin-right: center;
  display: grid;
  grid-template-columns: 4fr 1fr;
  grid-template-areas: 
    "header header"
    "catdata control";
}

#pageTitle {  
  margin-left: 300px;
  grid-area: header;
  margin-top: 50px;
  margin-bottom: 5px;
}

#userInput {
  background-color: rgb(40, 107, 138);
  
}
#progOutput td{
  border-style:solid;
  border-color: rgb(141, 131, 98);
  background-attachment: fixed;
}

h1{
  margin: top 20px;
  border-color: rgb(1, 212, 72);
  border-style:solid;
}

